<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-row :gutter="24">
            <!---->
            <el-col :xl="{'span':5}" :lg="{'span':5}" :md="{'span':24}" :sm="{'span':24}" :xs="{'span':24}" style="margin-bottom: 20px">
                <el-input placeholder="" v-model="filterText" style="margin-bottom: 20px">
                    <el-button slot="append" icon="el-icon-plus" @click="addCategory"/>
                </el-input>
                <el-tree :props="props"
                         @node-contextmenu="rightClick"
                         :filter-node-method="filterNode"
                         :highlight-current="highlight"
                         :data="categoryLists"
                         @node-click="getApiDetail"
                         default-expand-all
                         ref="tree"
                         id="tree"
                         :node-key="props.id"
                         style="background-color: #393d49">
                </el-tree>
            </el-col>
            <!---->

            <!---->
            <el-col :xl="{'span':18,'push':1}" :lg="{'span':18,'push':1}" :md="{'span':24}" :sm="{'span':24}" :xs="{'span':24}" v-show="apiVisible">
                <el-tabs type="border-card" v-model="apiName">
                    <el-tab-pane :label="apiName" :key="apiName" :name="apiName"/>
                    <el-card shadow="always">
                        <el-form :label-width="labelWidth" :model="apiModel" :ref="reFrom" :rules="rules">
                            <el-form-item label="" prop="type">
                                <el-select v-model="apiModel.type"  auto-complete="true" style="width: 100%" placeholder="">
                                    <el-option v-for="(category,index) in apiCategory" :key="index" :label="setName(category)" :value="category.id"/>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" prop="desc">
                                <el-input v-model="apiModel.desc" placeholder=""/>
                            </el-form-item>
                            <el-form-item label="" prop="href">
                                <el-input v-model="apiModel.href" placeholder=""/>
                            </el-form-item>
                            <el-form-item label="" prop="method">
                                <el-select v-model="apiModel.method" style="width: 100%" placeholder="">
                                    <el-option v-for="(method,index) in methodLists" :key="index" :label="method" :value="index"/>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" prop="request">
                                <el-button type="primary" style="margin-bottom: 5px" plain icon="el-icon-plus" @click="requestAdd()" size="medium"/>
                                <div v-for="(request,index) in apiModel.request" :key="index">
                                    <el-input v-model="request.name" auto-complete="true" style="width: 195px;margin-bottom: 5px" placeholder=""/>
                                    <el-select v-model="request.type"  auto-complete="true" style="width: 195px" placeholder="">
                                        <el-option v-for="(type,index) in typeLists" :key="index" :label="type" :value="index"/>
                                    </el-select>
                                    <el-select v-model="request.required" style="width: 195px" placeholder="">
                                        <el-option label="" value="1"/>
                                        <el-option label="" value="0"/>
                                    </el-select>
                                    <el-input v-model="request.desc" auto-complete="true" style="width: 195px" placeholder=""/>
                                    <el-input v-model="request.val" auto-complete="true" style="width: 195px" placeholder=""/>
                                    <el-button type="danger" plain icon="el-icon-delete" @click="requestRemove(request,index)" size="medium"/>
                                </div>
                            </el-form-item>
                            <el-form-item label="" prop="response_string">
                                <codemirror ref="edit" @change="updateContent" :value="apiModel.response_string"
                                            :options="options" style="line-height: 20px"/>
                            </el-form-item>
                            <el-form-item label="" prop="response">
                                <el-button type="primary" style="margin-bottom: 5px" plain icon="el-icon-plus" @click="responseAdd()" size="medium"/>
                                <div v-for="(response,index) in apiModel.response" :key="index">
                                    <el-input v-model="response.name" auto-complete="true" style="width: 195px;margin-bottom: 5px" placeholder=""/>
                                    <el-select v-model="response.type" auto-complete="true" style="width: 195px" placeholder="">
                                        <el-option v-for="(type,index) in typeLists" :key="index" :label="type" :value="index"/>
                                    </el-select>
                                    <el-input v-model="response.desc" auto-complete="true" style="width: 200px" placeholder=""/>
                                    <el-button type="danger" icon="el-icon-delete" plain @click="responseRemove(response,index)" size="medium"/>
                                </div>
                            </el-form-item>
                            <el-form-item label="" prop="remark">
                                <el-input v-model="apiModel.remark" maxlength="500" show-word-limit resize="none" :autosize="{ minRows: 4}" placeholder="" type="textarea"/>
                            </el-form-item>
                            <Submit :reFrom="reFrom" :model="apiModel" :url="url" :refs="refs" v-on:success="success"
                                    style="text-align: center"/>
                        </el-form>
                    </el-card>
                </el-tabs>
            </el-col>
            <!---->
        </el-row>

        <!---->
        <div v-show="menuVisible">
            <el-menu id="menu" class="menu" style="border-bottom: solid 1px #393d49" background-color="#393d49" text-color="#cccccc" mode="horizontal" active-text-color="#ffd04b">
                <el-menu-item v-show="btn.add" @click="addCategory"><i class="el-icon-circle-plus-outline"/> </el-menu-item>
                <el-menu-item v-show="btn.edit" @click="updateCategory"><i class="el-icon-edit-outline"/> </el-menu-item>
                <el-menu-item v-show="btn.del" @click="deleteCategory"><i class="el-icon-delete-solid"/> </el-menu-item>
            </el-menu>
        </div>
        <!---->

        <!----->
        <el-dialog :title="title" :visible.sync="syncVisible" :modal="modal"  :center="center" :width="dialogWidth">
            <el-form :label-width="labelWidth" :model="categoryModel" :ref="reFrom" :rules="rules">
                <el-form-item label="" prop="name" required>
                    <el-input v-model="categoryModel.name" placeholder=""/>
                </el-form-item>
                <el-form-item label="" prop="pid">
                    <el-select placeholder="" v-model="categoryModel.pid" style="width: 100%">
                        <el-option label="" value="0" v-if="categoryModel.pid === '0'" selected/>
                        <el-option v-for="(category,index) in apiCategory" :key="index" :label="setName(category)" :value="category.id"/>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <Submit :reFrom="reFrom" :model="categoryModel" :url="url" :refs="refs" v-on:success="success"/>
            </div>
        </el-dialog>
         <!----->

    </div>
</template>

<script>
    import apiLists from '../../api/api';
    import func from '../../api/func'
    import $url from '../../api/url';
    import Radio from "../common/Radio";
    import Delete from "../common/Delete";
    import Submit from "../common/Submit";
    import { codemirror } from 'vue-codemirror-lite'
    //
    require('codemirror/addon/hint/javascript-hint.js');
    require('codemirror/mode/javascript/javascript.js');
    require('codemirror/addon/selection/active-line');
    //
    require('codemirror/theme/monokai.css');
    //
    require('codemirror/addon/fold/foldgutter.css');
    require('codemirror/addon/fold/foldcode.js');
    require('codemirror/addon/fold/foldgutter.js');
    require('codemirror/addon/fold/brace-fold.js');
    require('codemirror/addon/fold/brace-fold.js');
    require('codemirror/addon/fold/comment-fold.js');
    //
    require('codemirror/addon/edit/matchbrackets.js');
    import {mapGetters,mapActions} from 'vuex'
    export default {
        name: "lists",
        components: {Submit, Delete, Radio,codemirror},
        data(){
            return {
                categoryLists:[],
                props:{
                    label:'name',
                    children:'children',
                    id:'id'
                },
                filterText:'',
                //
                title:'',
                syncVisible:false, //
                modal:true, //
                labelWidth:'80px',
                loading:true,
                center:true,
                loadingText:'',
                url:'',
                refs:this.$refs,
                reFrom:'category',
                //
                apiName:'',
                //
                menuVisible:false,
                categoryModel:{},
                apiCategory:[],
                highlight:true,
                //
                options:{
                    mode: 'application/ld+json',
                    //
                    tabSize: 4,
                    //
                    lineNumbers: true,
                    //theme
                    theme:'monokai',
                    //
                    extraKeys:{"Ctrl-Space":"autocomplete"},//ctrl-space
                    //
                    lineWrapping:false,
                    //
                    foldGutter: true,
                    gutters:["CodeMirror-linenumbers", "CodeMirror-foldgutter"],
                    // n*tabntabfalse
                    indentWithTabs: true,
                    //true
                    smartIndent: true,
                    //
                    matchBrackets:true,
                    // 
                    cursorHeight:1,
                    //
                    autoRefresh: true,
                    //
                    styleActiveLine:true,
                },
                //
                apiStatus:false,
                //
                methodLists:{
                    POST:'POST',
                    GET:'GET',
                    PUT:'PUT',
                    DELETE:'DELETE',
                },
                //
                typeLists:{
                    String:'String',
                    Float:'Float',
                    Number:'Number',
                    Object:'Object',
                    Array:'Array',
                    Timestamp:'Timestamp'
                },
                cgi:{
                    insert:$url.apiSave,
                    update:$url.apiUpdate,

                    categoryInsert:$url.categorySave,
                    categoryUpdate:$url.categoryUpdate,
                    remove:$url.categoryDelete,
                },
                //
                rules:{
                    type:[{required:true,message:'', trigger: 'change'}],
                    href:[{required:true,message:'', trigger: 'blur'},],
                    method:[{required:true,message:'', trigger: 'change'},],
                    desc:[{required:true,message:'', trigger: 'blur'},],
                    response_string:[{required:true,message:'', trigger: 'blur'},],
                },
                //
                scrollTop:0,
                //
                btn:{}
            }
        },
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        computed:{
            ...mapGetters(['apiVisible','apiModel','interfaceName','userInfo','dialogWidth'])
        },
        methods:{
            ...mapActions(['addApiVisible','addApiModel','saveSystemLog']),
            /**
             * todo
             */
            success:function(){
                this.getCategoryLists();
                this.syncVisible = false;
            },
            /**
             * TODO
             * @param value
             * @param data
             */
            filterNode:function(value, data) {
                if (!value) return true;
                return data.name.indexOf(value) !== -1;
            },
            /**
             * todo:API
             * @param item
             * @return {String}
             */
            setName:function(item){
                return Array(item.level+1).join('')+item.name;
            },
            /**
             * todoAPI
             */
            getCategoryLists:function () {
                apiLists.CategoryLists().then(response=>{
                    if (response && response.data.code === 200){
                        this.apiCategory = response.data.item.category;
                        this.categoryLists = response.data.item.category_tree;
                        this.loading = false;
                    }
                });
            },
            /**
             * todo
             * @param MouseEvent event 
             * @param object
             * @param Node  Node
             * @param element 
             */
            rightClick:function(MouseEvent, object, Node, element) {
                this.menuVisible = false;
                this.menuVisible = true;
                const menu = document.querySelector('#menu');
                const tree = document.getElementById('tree');
                this.scrollTop = func.get_scroll_top();
                document.addEventListener('click', this.foo);
                menu.style.left = tree.offsetWidth + 50 +'px';
                menu.style.top = MouseEvent.clientY + this.scrollTop - 125 + 'px';
                this.categoryModel = object;
            },
            /**
             * todo
             */
            foo:function() {
                this.menuVisible = false;
                document.removeEventListener('click', this.foo);
            },
            /**
             * todoAPI
             */
            addCategory:function () {
                this.title='';
                this.syncVisible = true;
                this.url = this.cgi.categoryInsert;
                this.categoryModel = {name:'', pid:this.categoryModel.id === undefined ? '0':this.categoryModel.id, path:'1', level:1};
            },
            /**
             * todoAPI
             */
            updateCategory:function () {
                this.title = this.categoryModel.name;
                this.syncVisible = true;
                this.url = this.cgi.categoryUpdate;
            },
            /**
             * TODO:API
             */
            deleteCategory:function(){
                apiLists.CategoryDelete({id:this.categoryModel.id}).then(response=>{
                    if (response && response.data.code === 200){
                        let data = { href:this.cgi.remove, msg:response.data.msg,token:this.$store.state.login.token };
                        this.saveSystemLog(data);
                        this.$message({type:'success',message:response.data.msg});
                        this.getCategoryLists()
                    }
                })
            },
            /**
             * 
             * @param data
             */
            getApiDetail:function(data){
                if (data.level<=1){
                    this.$notify({title: '', message: '', type: 'success'});
                    return;
                }
                const obj = { 'apiVisible':true,'interfaceName':data.name };
                //
                this.addApiVisible(obj);
                this.apiName = this.interfaceName;
                apiLists.ApiLists( {type:data.id} ).then(response=>{
                    if (response && response.data.code===200){
                        this.addApiModel(response.data.item);
                        this.apiModel.request = JSON.parse(this.apiModel.request);
                        this.apiModel.response = JSON.parse(this.apiModel.response);
                        this.url = this.cgi.update
                    } else {
                        this.url = this.cgi.insert;
                        let apiModel = {desc:'', type:data.id, href:'', method:'POST', request:[{"name":"token","desc":"token","required":"1","type":"String","val":""}],
                            response:[{"name":"code","desc":"200 ","type":"Number"},{"name":"msg","desc":"Success","type":"String"}],
                            response_string:'', remark:''};
                        this.addApiModel(apiModel);
                    }
                });
            },
            /**
             * todo
             * @param item
             * @param index
             */
            requestRemove:function(item,index){
                let request=[],that = this;
                Object.keys(that.apiModel.request).forEach(function (item) {
                    request.push(that.apiModel.request[item]);
                });
                request.splice(index,1);
                that.apiModel.request = request;
            },
            /**
             * todo
             */
            requestAdd:function(){
                this.apiModel.request.push({name:'', desc:'', required:'', type:'', val:''});
            },
            /**
             * todo
             * @param item
             * @param index
             */
            responseRemove:function(item,index){
                let response=[],that = this;
                Object.keys(that.apiModel.response).forEach(function (item) {
                    response.push(that.apiModel.response[item]);
                });
                response.splice(index,1);
                that.apiModel.response = response;
            },
            /**
             * todo
             */
            responseAdd:function(){
                this.apiModel.response.push({name:'', desc:'',  type:'' });
            },
            /**
             * todo
             * @param content
             */
            updateContent:function(content){
                this.apiModel.response_string = content;
            },
        },
        created(){
            this.apiName = this.interfaceName;
            if (this.apiModel.id) {
                this.url = this.cgi.update
            } else {
                this.url = this.cgi.insert
            }
        },
        mounted() {
            this.$nextTick(function () {
                this.btn = func.set_btn_status(this.$route.path,this.$route.name,this.userInfo.auth);
                this.getCategoryLists();
            });
        }
    }
</script>

<style scoped>

</style>



