<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-form :inline="true" style="margin-top: 10px">
            <el-form-item>
                <el-button v-for="(state,index) in pushState" :key="index" @click="getState(state.val)" :type="state.type" size="medium" plain>{{ state.val.toUpperCase() }}</el-button>
            </el-form-item>
            <el-form-item style="float:right;" v-if="btn.add">
                <el-button icon="el-icon-plus" type="primary" size="medium" plain @click="addPush"> </el-button>
            </el-form-item>
        </el-form>
        <el-table :data="pushLists.filter(data=>(!search || data.username.toLowerCase().includes(search.toLowerCase()) || data.info.toLowerCase().includes(search.toLowerCase())))">
            <el-table-column label="#" prop="id" width="100px"/>
            <el-table-column label="" prop="username" width="120px"/>
            <el-table-column label="" prop="title" width="150px"/>
            <el-table-column label="" prop="info" :show-overflow-tooltip="true"> </el-table-column>
            <el-table-column label="" width="80px"  :filters="[{ text: '', value: '1' }, { text: '', value: '2' }]"
                             :filter-method="filterStatus">
                <template slot-scope="scope">
                    <el-button plain type="primary" size="mini" v-if="scope.row.status === 2"></el-button>
                    <el-button plain type="success" size="mini"  v-if="scope.row.status === 1"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" prop="state" width="150px">
                <template slot-scope="scope">
                    <el-button plain :type="setType(scope.row.state)" size="mini">{{scope.row.state.toUpperCase()}}</el-button>
                </template>
            </el-table-column>
            <el-table-column label="" prop="created_at" width="200px"/>
            <el-table-column width="200px" align="right">
                <template slot="header" slot-scope="scope">
                    <el-input v-model="search" placeholder=""/>
                </template>
                <template slot-scope="scope">
                    <el-button type="primary" v-if="scope.row.state!=='successfully' && btn.edit" plain icon="el-icon-edit" size="mini" @click="updatePush(scope.row)"> </el-button>
                    <Delete :url="cgi.remove" :item="scope.row" :index="scope.$index" :Lists="pushLists"
                            v-on:success="success" v-if="btn.del"/>
                </template>
            </el-table-column>
        </el-table>
        <div style="margin: 25px 0">
            <el-pagination
                    @size-change="sizeChange"
                    @current-change="currentChange"
                    :page-sizes="[15, 30, 50, 100]"
                    :page-size="limit"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    :current-page="page">
            </el-pagination>
        </div>
        <!--table -->
        <!----->
        <el-dialog :title="title" :width="dialogWidth" :visible.sync="syncVisible" :modal="modal" :center="center" :destroy-on-close="destroy_on_close">
            <el-form :label-width="labelWidth" :model="pushModel" :ref="reFrom" :rules="rules">
                <el-form-item label="" prop="username">
                    <el-select v-model="pushModel.username" filterable @change="changeOauthName" style="width: 100%">
                        <el-option label="" value="all"/>
                        <el-option v-for="(push,index) in oauthLists" :key="index" :label="push.username"
                                   :value="push.uuid"/>
                    </el-select>
                </el-form-item>
                <el-form-item label="UUID" prop="uid">
                    <el-input v-model="pushModel.uid" readonly placeholder="UUID"/>
                </el-form-item>
                <el-form-item label="" prop="title">
                    <el-input v-model="pushModel.title" placeholder=""/>
                </el-form-item>
                <el-form-item label="" prop="created_at">
                    <el-date-picker type="datetime" value-format="yyyy-MM-dd HH:mm:ss" format="yyyy-MM-dd HH:mm:ss"  v-model="pushModel.created_at"
                                    style="width: 100%"/>
                </el-form-item>
                <el-form-item label="" prop="info">
                    <el-input v-model="pushModel.info" placeholder="" type="textarea" resize="none" rows="4"/>
                </el-form-item>
                <el-form-item label="" prop="status">
                    <el-radio-group v-model="pushModel.status" size="small">
                        <el-radio-button label="2"></el-radio-button>
                        <el-radio-button label="1"></el-radio-button>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <Submit :reFrom="reFrom" :model="pushModel" :url="url" :refs="refs" v-on:success="success"/>
            </div>
        </el-dialog>
        <!----->
    </div>
</template>

<script>
    import apiLists from '../../api/api';
    import $url from '../../api/url';
    import func from '../../api/func'
    import Radio from "../common/Radio";
    import Delete from "../common/Delete";
    import Submit from "../common/Submit";
    import {mapGetters} from 'vuex'
    export default {
        name: "lists",
        components: {Submit, Delete, Radio},
        data(){
            return {
                pushLists:[],
                page:1,
                limit:15,
                state:'',
                status:'',
                total:0,
                search:'',

                oauthLists:[],

                title:'',
                syncVisible:false, //
                modal:true, //
                labelWidth:'80px',
                loading:true,
                destroy_on_close:true,
                center:true,
                loadingText:'',

                url:'',
                refs:this.$refs,
                reFrom:'push',

                pushModel:{},

                cgi:{
                    insert:$url.pushSave,
                    update:$url.pushUpdate,
                    remove:$url.pushDelete,
                    status:$url.pushUpdate
                },
                rules:{
                    info:[{required:true,message:'',trigger:'blur'}],
                    title:[{required:true,message:'',trigger:'blur'}]
                },
                pushState:[
                    {'val':'successfully','label':'','type':'success'},
                    {'val':'failed','label':'','type':'danger'},
                    {'val':'offline','label':'','type':'default'}
                ],
                //
                btn:{},
            }
        },
        computed:{
            ...mapGetters(['userInfo','dialogWidth']),
        },
        methods:{
            /**
             * todo
             */
            success:function(){
                this.syncVisible = false;
                this.getPushLists(this.page,this.limit)
            },
            /**
             * TODO:
             * @param value
             * @param row
             */
            filterStatus:function(value, row) {
                return row.status.toString() === value;
            },
            /**
             * TODO:
             * @param state
             */
            setType:function(state) {
                let type = '';
                switch (state) {
                    case 'failed'      :  type = 'danger';   break;
                    case 'successfully':  type = 'success';  break;
                    case 'offline'     :  type = 'default';  break;
                }
                return type;
            },
            /**
             * todo
             * @param page
             * @param limit
             */
            getPushLists:function (page,limit) {
                let params = { page:page,limit:limit,state:this.state,status:this.status };
                apiLists.PushList(params).then(response=>{
                    this.pushLists = response.data.item.data;
                    this.total = response.data.item.total;
                    this.oauthLists =response.data.item.oauth
                });
                this.loading = false;
            },
            /**
             * TODOID
             * @param item
             */
            changeOauthName:function(item) {
                this.pushModel.uid = item!=='all' ? item : ''
            },
            /**
             * TODO
             * @param state
             */
            getState:function(state) {
               this.state = state;
               this.getPushLists(1,this.limit)
            },
            /**
             * todo
             * @param val
             */
            sizeChange:function(val){
                this.limit = val;
                this.getPushLists(this.page,this.limit)
            },
            /**
             * todo
             * @param val
             */
            currentChange:function(val){
                this.page = val;
                this.getPushLists(this.page,this.limit)
            },
            /**
             * todo
             */
            addPush:function () {
                this.title='';
                this.syncVisible = true;
                this.pushModel = {
                    username:'all',
                    uid:'',
                    state:'',
                    status:2,
                    created_at:func.set_time(new Date()),
                    title:'',
                };
                this.url = this.cgi.insert;
            },
            /**
             * todo
             * @param item
             */
            updatePush:function (item) {
                this.title='';
                this.syncVisible = true;
                this.pushModel = item;
                this.url = this.cgi.update;
            }
        },
        mounted() {
            this.$nextTick(function () {
                this.btn = func.set_btn_status(this.$route.path,this.$route.name,this.userInfo.auth);
                this.getPushLists(this.page,this.limit)
            });
        }
    }
</script>

<style scoped>

</style>



