<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-form :inline="true" style="margin-top: 10px" v-if="btn.add">
            <el-form-item style="float:right;">
                <el-button icon="el-icon-plus" type="primary" size="medium" plain @click="addReqRule"> </el-button>
            </el-form-item>
        </el-form>
        <el-table :data="reqRuleLists.filter(data=>(!search || data.username.toLowerCase().includes(search.toLowerCase()) || data.href.includes(search.toLowerCase())))">
            <el-table-column label="#ID" prop="id" width="70px" align="center" sortable/>
            <el-table-column label="" prop="username" width="130px" :show-tooltip-when-overflow="true" align="center"/>
            <el-table-column label="" prop="href" align="center" :show-tooltip-when-overflow="true"/>
            <el-table-column label="" align="center" v-if="userInfo.username === 'admin'">
                <template slot-scope="scope">
                    <Radio :item="scope.row" :url="cgi.status" v-on:success="success"/>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" v-if="userInfo.username !== 'admin'">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.status === 1" type="success" size="mini"></el-button>
                    <el-button v-if="scope.row.status === 2" type="info" size="mini"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" prop="created_at"  align="center" width="160px" sortable/>
            <el-table-column label="" prop="updated_at" align="center" width="160px" sortable/>
            <el-table-column label="" prop="expires" align="center" width="160px" sortable/>
            <el-table-column label="" width="200px" align="right">
                <template slot="header" slot-scope="scope">
                    <el-input v-model="search" placeholder=""/>
                </template>
                <template slot-scope="scope">
                    <el-button type="primary" v-if="scope.row.status === 1 && btn.edit" plain icon="el-icon-plus" size="mini" @click="updateReqRule(scope.row)"> </el-button>
                    <Delete :url="cgi.remove" :item="scope.row" :index="scope.$index" :Lists="reqRuleLists" v-on:success="success" v-if="btn.del"/>
                </template>
            </el-table-column>
        </el-table>
        <div style="margin: 25px 0">
            <el-pagination
                    @size-change="sizeChange"
                    @current-change="currentChange"
                    :page-sizes="[15, 30, 50, 100]"
                    :page-size="limit"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    :current-page="page">
            </el-pagination>
        </div>
        <!--table -->
        <!----->
        <el-dialog :title="title" :visible.sync="syncVisible" :modal="modal" :width="dialogWidth" :center="center" :destroy-on-close="destroy_on_close">
            <el-form :label-width="labelWidth" :model="reqRuleModel" :ref="reFrom" :rules="rules">
                <el-form-item label="" prop="username">
                    <el-select filterable style="width: 100%" v-model="reqRuleModel.username" @change="getAuth">
                        <el-option v-for="(user,index) in userLists" :label="user.username" :key="index"
                                   :value="user.id"/>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="href" v-if="href">
                    <el-select multiple="multiple" filterable style="width: 100%" v-model="reqRuleModel.href">
                        <el-option v-for="(rule,index) in ruleLists" :label="setAuthName(rule)" :key="index"
                                   :value="rule.href" :disabled="rule.disable"/>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="expires">
                    <el-date-picker v-model="reqRuleModel.expires" type="datetime" value-format="yyyy-MM-dd HH:mm:ss"
                                    placeholder=""
                                    :picker-options="pickerOptions"
                                    style="width: 100%"/>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="reqRuleModel.desc" maxlength="200" show-word-limit resize="none" :autosize="{ minRows: 4}" type="textarea"/>
                </el-form-item>
                <el-form-item label="" prop="status" v-if="userInfo.username==='admin'">
                    <el-radio-group v-model="reqRuleModel.status" size="small">
                        <el-radio-button label="2"></el-radio-button>
                        <el-radio-button label="1"></el-radio-button>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <Submit :reFrom="reFrom" :model="reqRuleModel" :url="url" :refs="refs" v-on:success="success"/>
            </div>
        </el-dialog>
        <!----->
    </div>
</template>

<script>
    import apiLists from '../../api/api';
    import $url from '../../api/url';
    import func from '../../api/func'
    import Radio from "../common/Radio";
    import Delete from "../common/Delete";
    import Submit from "../common/Submit";
    import {mapGetters} from 'vuex';
    export default {
        name: "lists",
        components: {Submit, Delete, Radio},
        data(){
            return {
                reqRuleLists:[],
                ruleLists:[], //
                userLists:[], //
                page:1,
                limit:15,
                total:0,
                search:'',

                title:'',
                syncVisible:false, //
                modal:true, //
                labelWidth:'80px',
                loading:true,
                destroy_on_close:true,
                center:true,
                loadingText:'',

                url:'',
                refs:this.$refs,
                reFrom:'reqRule',

                reqRuleModel:{},

                cgi:{
                    remove:$url.reqRuleDelete,
                    status:$url.reqRuleUpdate,
                    update:$url.reqRuleUpdate,
                    insert:$url.reqRuleSave
                },
                rules:{
                    username:[{required:true,message:'',trigger:'blur'}],
                    href:[{required:true,message:'',trigger:'change'}],
                    expires: [{required:true,message:'',trigger:'change'}],
                    desc:[{required:true,message:'',trigger:'blur'}],
                    status:[{required:true,message:'',trigger:'blur'}]
                },
                href:false,

                //
                pickerOptions: {
                    disabledDate(time) {
                        return time.getTime() < Date.now();
                    },
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', date);
                        }
                    },{
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24 * 30 * 3);
                            picker.$emit('pick', date);
                        }
                    },{
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24 * 30 * 6);
                            picker.$emit('pick', date);
                        }
                    },{
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24 * 30 * 12);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                //
                btn:{}
            }
        },
        computed:{
            ...mapGetters(['userInfo','dialogWidth'])
        },
        methods:{
            /**
             * todo
             */
            success:function(){
                this.syncVisible = false;
                this.getReqRuleLists(this.page,this.limit)
            },
            /**
             * todo
             * @param timestamp
             */
            setTimes:function(timestamp){
                return func.set_time(timestamp*1000);
            },
            /**
             * todo
             * @param page
             * @param limit
             */
            getReqRuleLists:function (page,limit) {
                let params = { page:page,limit:limit };
                this.loading = true;
                apiLists.ReqRuleLists(params).then(response=>{
                    if (response && response.data.code === 200){
                        this.reqRuleLists = response.data.item.data;
                        this.total = response.data.item.total;
                        this.userLists = response.data.item.userLists;
                        this.loading = false;
                    }
                });
            },
            /**
             * 
             * @param item
             * @return {String}
             */
            setAuthName:function(item){
                return Array(item.level+1).join('')+item.name;
            },
            /**
             * todo
             * @param val
             */
            sizeChange:function(val){
                this.limit = val;
                this.getReqRuleLists(this.page,this.limit)
            },
            /**
             * todo
             * @param val
             */
            currentChange:function(val) {
                this.page = val;
                this.getReqRuleLists(this.page,this.limit)
            },
            /**
             * todo
             */
            addReqRule:function() {
                this.title='';
                this.syncVisible = true;
                this.reqRuleModel = { username:'', href:'', desc:'', expires:'', status:1 };
                this.url = this.cgi.insert;
                this.href = false;
            },
            /**
             * TODO:
             */
            getAuth:function(user_id){
                let params = {user_id:user_id}
                apiLists.GetAuthByToken(params).then(response=>{
                    if (response && response.data.code === 200) {
                        this.ruleLists = response.data.item;
                        this.href = true;
                    }
                });
            },
            /**
             * todo
             * @param item
             */
            updateReqRule:function (item) {
                this.title='';
                this.syncVisible = true;
                this.reqRuleModel = item;
                this.reqRuleModel.href = [this.reqRuleModel.href];
                this.ruleLists = this.reqRuleModel.ruleLists
                this.url = this.cgi.update;
                this.href = true;
            }
        },
        mounted() {
            this.$nextTick(function () {
                this.btn = func.set_btn_status(this.$route.path,this.$route.name,this.userInfo.auth);
                this.getReqRuleLists(this.page,this.limit)
            });
        }
    }
</script>

<style scoped>

</style>



