<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-form :label-width="labelWidth" :model="userModel" :ref="reFrom" :rules="rules">
            <el-form-item label="" prop="username">
                <el-input v-model.trim="userModel.username" placeholder=""/>
            </el-form-item>
            <el-form-item label="" prop="avatar_url">
                <Upload :avatar_url="userModel.avatar_url" :username="userModel.username"
                        @uploadSuccess="uploadSuccess"/>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input :type="!showPassword ? 'password' : 'text'" placeholder="" v-model="userModel.password" :readonly="!showPassword">
                    <el-button slot="append" :icon="showPassword ? 'el-icon-open' : 'el-icon-turn-off'" @click="showPassword = !showPassword">
                        {{showPassword ? ' ' : ' '}}
                    </el-button>
                </el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model.trim="userModel.email" type="email" placeholder=""/>
            </el-form-item>
            <el-form-item label="" prop="phone_number">
                <el-input v-model.number="userModel.phone_number" placeholder=""/>
            </el-form-item>
            <el-form-item label="" prop="status">
                <el-radio-group  v-model="userModel.status" size="small">
                    <el-radio-button label="2"></el-radio-button>
                    <el-radio-button label="1"></el-radio-button>
                </el-radio-group>
            </el-form-item>
            <el-form-item style="text-align: center">
                <Submit :reFrom="reFrom" :model="userModel" :url="url" :refs="refs"/>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import apiLists from '../../api/api';
    import $url from '../../api/url';
    import func from '../../api/func'
    import Submit from "../common/Submit";
    import Upload from '../common/Upload'
    import {mapGetters} from 'vuex';
    export default {
        name: "lists",
        components: {Submit,Upload},
        data(){
            return {
                labelWidth:'80px',
                loading:false,
                loadingText:'',

                url:$url.userUpdate,
                refs:this.$refs,
                reFrom:'user',
                showPassword:false,
                userModel:{
                    username:'',
                    email:'',
                    password:'',
                    salt:func.set_random(),
                    status:1,
                    role_id:2,
                    phone_number:'',
                    created_at:func.get_timestamp(),
                    updated_at:func.get_timestamp(),
                    avatar_url:''
                },
                rules:{
                    username:[{required:true,message:'',trigger:'blur'}],
                    password:[{required:true,message:'',trigger:'blur'}],
                    email:[{required:true,message:'',trigger:'blur'},{type:'email',message:'',trigger:'blur'}],
                    status:[{required:true,message:'',trigger:'change'}],
                    avatar_url:[{required:true,message:'',trigger:'change'}],
                },
            }
        },
        computed:{
            ...mapGetters(['userInfo']),
        },
        methods:{
            /**
             * TODO:
             */
            userBind:function() {
                apiLists.UserBind({'remember_token':this.userInfo.token}).then(response=>{
                    if (response && response.data.code===200){
                        this.userModel = response.data.item;
                    }
                })
            },
            /**
             * TODO:
             * @param src
             */
            uploadSuccess:function(src) {
                this.userModel.avatar_url = src;
            },
        },
        mounted() {
            this.$nextTick(function () {
                this.userBind();
            })
        },
        created(){
            this.userModel.username = this.userInfo.username;
            this.userModel.avatar_url = this.userInfo.avatar_url;
            this.userModel.remember_token = this.userInfo.token;
        },
    }
</script>

<style scoped>

</style>



